@font-face {
    font-family: 'turn-arrow';
    src: url('./font/icon/iconfont.eot');
    src: url('./font/icon/iconfont.eot?#iefix') format('embedded-opentype'),
    url('./font/icon/iconfont.woff') format('woff'),
    url('./font/icon/iconfont.ttf') format('truetype'),
    url('./font/icon/iconfont.svg#iconfont') format('svg');
}

html, body {
  height: 100%;
  width: 100%;
  background-color: #ddd;
}

.root {
  width: 100%;
  height: 100%;
}

.stage {
  width: 100%;
  height: 800px;
  position: relative;
}

.image-area {
  height: 100%;
  position: relative;
  overflow: hidden;
}


.figure-wrap {
  position: absolute;
  margin: 0;
  width: 320px;
  height: 360px;
  padding: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background-color: #eee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;

  -webkit-transform-origin: 0 0 0;

      -ms-transform-origin: 0 0 0;

          transform-origin: 0 0 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  -o-transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
}

.is-inverse {
  -webkit-transform: translateX(320px) rotateY(180deg);
          transform: translateX(320px) rotateY(180deg);
}

.figure-image {
  width: 240px;
  height: 240px;
}

.figure-title {
  font-size: 16px;
  font-weight: bold;
  margin-top: 20px;
}

.figure-desc {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  background-color: #fff;
}


.controller {
  position: absolute;
  bottom: 30px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 111
}

.control-unit {
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  background-color: #aaa;
  margin: 0 5px;
  -webkit-transform: scale(.5);
      -ms-transform: scale(.5);
          transform: scale(.5);
  cursor: pointer;
  -webkit-transition: background-color 0.3s, -webkit-transform .6s ease-in-out;
  transition: background-color 0.3s, -webkit-transform .6s ease-in-out;
  -o-transition: transform .6s ease-in-out, background-color 0.3s;
  transition: transform .6s ease-in-out, background-color 0.3s;
  transition: transform .6s ease-in-out, background-color 0.3s, -webkit-transform .6s ease-in-out;
}

.is-center {
  background-color: #888;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.is-center::after {
  font-family: "turn-arrow" !important;
  font-size: 16px;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  content: "\e6b4";
}








